.approach {
    background-color: var(--background-section-color);
    padding: 30px 0 50px 0;
 

    &__title {
      font-family: var(--main-font);
      font-weight: 800;
      font-size: 20px;
      line-height: 1.3;
      text-align: center;
      letter-spacing: 0.03em;
      color: var(--primary-dark-color);
      margin-bottom:30px;
    
    }

    &__list {
      padding: 0;
      margin: 0;
      list-style: none;
    }

    &__item {
      background-color: var(--primary-white-color);
      color: var(--accent-color);
      padding-top:30px;
      padding-bottom:22px;
      box-shadow: 0px 8px 26px 0px #0000001A;
      box-shadow: 0px 4px 4px 0px #00000040;
      width: 280px;
      padding: 30px 11px 22px 11px;
      margin: auto;

      &:not(:last-child) {
        margin-bottom: 15px;
      }

      &:hover,
      &:focus{
          background-color: var(--accent-color);
          color: var(--primary-white-color);
      }
    }

    &__item:hover > &__item-text,
    &__item:focus > &__item-text,
    &__item:hover > &__item-title,
    &__item:focus > &__item-title
    {
        color: var(--primary-white-color);
    }

    &__item:hover > .delimeter,
    &__item:focus > .delimeter
    {
        background: var(--primary-white-color);
    }

    &__item-text {
      font-family: var(--main-font);
      font-weight: 500;
      font-size: 12px;
      line-height: 1.7;
      text-align: center;
      letter-spacing: 0.08em;
      color: var(--primary-grey-color);
    }

    &__item-title {
      font-family: var(--main-font);
      font-weight: bold;
      font-size: 14px;
      line-height: 2;
      text-align: center;
      letter-spacing: 0.08em;
      color: var(--primary-dark-color);
      position: relative;
    //   height:56px;

      margin-bottom: 22px;

      &:after{
          content: "";
          display: block;
        width: 20px;
        height: 2px;
        background-color:var(--accent-color);
        margin: 20px auto 0;
      }

    }

    &__icon {
       display:flex;
       margin-left:auto;
       margin-right:auto;
       fill: currentColor;
       margin-bottom: 20px;
    }
}

.delimeter {
    width: 20px;
    height: 2px;
    background-color:var(--accent-color);
    margin: 20px auto;
}

  @media screen and (min-width: 768px) {
      .approach {
          &__list {
            display: flex;
            flex-wrap: wrap;
            margin-left: -20px;
            margin-top:-20px;
          }
          &__item {
            flex-basis: calc(100%/2 - 20px);
            margin-left:20px;
            margin-top:20px;
            width:334px;
            padding: 40px 22px 48px 22px;

            &:not(:last-child) {
                margin-bottom: 0;
            }
          }
          &__title {
            font-size: 26px;
            font-weight: 800;
            line-height: 1.30;
            letter-spacing: 0.03em;
          }
          &__item-text {
            font-size: 14px;
            font-weight: 700;
            line-height: 1.42;
            letter-spacing: 0.08em;
          }
          &__item-text {
            font-size: 12px;
            font-weight: 500;
            line-height: 1.66;
            letter-spacing: 0.08em;
  
          }
      }
    
}
  @media screen and (min-width:1366px) {
      .approach {
          &__title{
                font-weight: 800;
                font-size: 46px;
                line-height: 1.30;
                letter-spacing: 0.03em;
                text-align: start;
          }
          &__item {
                flex-basis: calc(100%/3 - 20px);
                width:424px;
                padding: 40px 30px 55px 30px;
          }
            &__item-title{
                font-size: 20px;
                font-weight: 700;
                line-height: 1.4;
                letter-spacing: 0.08em;
          }
         &__item-text {
                font-size: 18px;
                font-weight: 500;
                line-height: 1.55;
                letter-spacing: 0.08em;
         }
      }
  }