.guarantee {
  padding-top: 25px;
  padding-bottom: 25px;

  @media screen and (min-width: 1366px) {
    padding-top: 65px;
    padding-bottom: 65px;
  }
}

.guarantee__title {
  text-align: center;
  font-size: 26px;
  color: var(--primary-dark-color);

  @media screen and (min-width: 1366px) {
    font-size: 46px;
  }
}

.guarantee__container {
  background-color: var(--background-section-color);
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;

  @media screen and (min-width: 768px) {
    padding-left: 40px;
    padding-right: 40px;
  }

  @media screen and (min-width: 1366px) {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.guarantee__text {
  position: relative;
  font-size: 12px;
  color: var(--secondary-grey-color);
  line-height: 1.67;
  padding-left: 20px;

  @media screen and (min-width: 768px) {
    padding-left: 24px;
  }

  @media screen and (min-width: 1366px) {
    font-size: 20px;
    line-height: 1.4;
    padding-left: 30px;
  }
}

.accent-color {
  color: var(--accent-color);
}

.guarantee__list {
  margin-top: 30px;
  padding-top: 50px;
  padding-bottom: 50px;

  @media screen and (min-width: 768px) {
    padding-top: 30px;
    padding-left: 79px;
    padding-right: 50px;
    padding-bottom: 30px;
  }
}

@media screen and (min-width: 1366px) {
  .guarantee__list {
    padding-top: 50px;
    padding-right: 112px;
    padding-left: 112px;
    padding-bottom: 45px;
    background-color: var(--background-section-color);
  }
}

.guarantee__item {
  font-size: 12px;
  line-height: 40px;
  padding-right: 20px;

  @media screen and (min-width: 768px) {
    padding-right: 30px;
  }
}

@media screen and (min-width: 1366px) {
  .guarantee__item {
    padding-left: 30px;
  }

  .guarantee__item:not(:last-child) {
    padding-bottom: 23px;
    border-left: 1px dashed;

    @media screen and (min-width: 768px) {
      padding-bottom: 20px;
    }
  }

  .guarantee__item::before {
    display: block;
    content: '';
    background-image: url('../images/fire.svg');
    background-size: contain;
    background-repeat: no-repeat;

    min-width: 15px;
    height: 20px;
    position: absolute;
    left: 14px;
  }

  @media screen and (min-width: 768px) {
    .guarantee__item::before {
      left: 113px;
    }
  }

  @media screen and (min-width: 1366px) {
    .guarantee__item::before {
      min-width: 22px;
      height: 30px;
      left: 118px;
    }
  }
}
